export const useLmsDashboardMaster = (args) => {
  const { data, refetch, isFetching } = useLmsDashboardMasterQuery(args.data, {
    skip: args.skip,
  });
  return {
    lmsDashboardMaster: data,
    lmsDashboardMasterRefetch: refetch,
    lmsDashboardMasterLoading: isFetching,
  };
};

