import { POST, PATCH, DELETE } from "../UrlConfig/MethodType";

export const lmsActivateModuleFunction = (builder) => {
  return builder.mutation({
    query: (data) => ({
      url: `/lms/ins/lms/query`,
      method: POST,
      body: data.activateModule,
    }),
  });
};
export const lmsUpdateModuleFunction = (builder) => {
  return builder.mutation({
    query: (data) => ({
      url: `/edit/staff/lms/staff/${data.osid}?nsid=${data.nsid}`,
      method: PATCH,
    }),
    invalidatesTags: ["Lms_Module"],
  });
};

export const lmsDashboardMasterFunction = (builder) => {
  return builder.query({
    query: (data) => `/lms/${data.lmid}/dashboard?mod_id=${data.mid}`,
    transformResponse: (response) => response?.lms,
    providesTags: ["Lms_Module"],
    serializeQueryArgs: ({ endpointName }) => {
      return endpointName;
    },
    forceRefetch({ currentArg, previousArg }) {
      return currentArg !== previousArg;
    },
  });
};

